<template>
  <div class="page-container">
    <fg-header title="攻略" />

    <user-info class="user-info" uStar ></user-info>
    <div class="xinagqing">
      <h1 class="title">{{text.title}}</h1>
       <div v-html="text.html"></div>
    </div>
   
  </div>
</template>

<script>
import { articlesDetail } from '@/api/home'
  export default {
    data() {
      return {
        text:'',
      }
    },
    created(){
    },
  mounted() {
   this.int()
  },
  methods: {
    int(){
      let data={
        id:this.$route.params.title
      }
       articlesDetail(data).then((res) => {
         if(res.data.code=='success'){ this.text= res.data.data}else{this.$toast(res.data.msg)}
         
        }).catch((error) => {reject(error)})
    },
  }
  }
</script>

