<template>
  <div class="beijinTY">
   <fg-header title="钱包"></fg-header>
   <div>
     <ul class="marT50 listul">
       <li class="listLi">
         <div class="neirong">
            <div class="listtext bibi">
               <p>币币账户余额</p>
               <p class="gtf">{{display_amount}}GTF</p>
             </div>
             <div class="listtext yxbibi">
               <p>游戏账户余额</p>
               <p class="gtf">{{info.candy_display}}GTF</p>
             </div>
           <div class="bi">
             <div class="marL40 listtext">
              <p class="textcenter">可用</p>
              <p class="textcenter">{{info.candy_available_display}}GTF</p>
              </div>
              <div class="listtext clearfix">
                  <p class="textcenter">冻结</p>
                  <p class="textcenter">{{info.candy_frozen_display}}GTF</p>
              </div>
           </div>
           
           <div class="buns">
             <div class="listbtn chongzhi   buttext" @click="btnclick('充值')">充值</div>
             <div class="listbtn chufa   buttext" @click="btnclick('提取')">提取</div>
             <div class="listbtn huazhuang   buttext" @click="popupHZ=true">划转</div>
           </div>
         </div>
       </li>
     </ul>
     <div class="listUL">

     
     <van-tabs v-model="active"  color='#884416' background="#DFC48B" title-inactive-color="#fff" title-active-color='#884416'  @change="onClick">
      <van-tab title="游戏账户明细" >
        <ul  v-if='list.length!=0'>
          <li class="listtext jiluli" v-for="item in list" :key='item.id'>
            <div>
              <p>{{item.amount}} GTF</p>
              <p>{{item.flow_type_name}}</p>
            </div>
            <div>{{item.created_at}}</div>
          </li>
        </ul>
      </van-tab>
      <van-tab title="币币账户明细">
        <ul  v-if='list.length!=0'>
          <li class="listtext jiluli" v-for="item in list" :key='item.id'>
            <div>
              <p>{{item.amount}} GTF</p>
              <p>{{item.flow_type_name}}</p>
            </div>
            <div>{{item.created_at}}</div>
          </li>
        </ul>
      </van-tab>
    </van-tabs>
     <van-pagination 
             class="pagination"
            v-model="currentPage" 
              :show-page-size="2"
              force-ellipses
              :total-items='total'
              :items-per-page='limit'
            @change="asset_flowList()"
      />
    </div>
   </div>
     <van-popup v-model="popupshow">
        <div class="yaoqingbg" >
          <h1 class="yaoqingtitle">{{popupTitle}}
           <span class="yaoqingtitle" v-if="this.popupTitle=='提取'">币币</span> 
           <span class="yaoqingtitle" v-if="this.popupTitle=='充值'">游戏</span>
          </h1>
          <div class="copy_text" >
            <p>填写{{popupTitle}}
              <span v-if="this.popupTitle=='提取'">币币</span> 
              <span v-if="this.popupTitle=='充值'">游戏</span>
              数量</p>
           <input type="number" v-model="TQamount"  class="inputStyle" :placeholder="placeholder">
            <div v-if="this.popupTitle=='提取'">可用余额：{{display_amount}}GTF</div>
            <div v-if="this.popupTitle=='充值'">可用余额：{{czdisplay_amount}}GTF</div>
          </div>
          <div class="btnline">
            <div class='fuzhi' @click="tiqu()">确认{{popupTitle}}</div>
          </div>
        </div>
