      <van-popup v-model="popupHZ">
        <div class="fangjianbg" >
          <h1 class="yaoqingtitle">划转</h1>
          <div class="copy_text" >
            <p>选择划转方向</p>
            <van-radio-group v-model="radio" class="radio">
              <van-radio name="1" checked-color="#A53C18" icon-size='15px'>游戏账户 => 币币账户</van-radio>
              <br>
              <van-radio name="2" checked-color="#A53C18"  icon-size='15px'>游戏账户 &lt;= 币币账户</van-radio>
            </van-radio-group>
            <p>填写划转数量</p>
           <input type="number" v-model="HZamount"  class="inputStyle" placeholder="填写划转数量">
          </div>
          <div class="btnsline">
            <div class='fuzhi' @click="huazhuang()">确认划转</div>
          </div>
        </div>
      </van-popup>
  </div>
</template>
<script type='text/ecmascript-6'>
import { asset_flowList,transferscreate} from '@/api/tools'
import { getUserInfo} from '@/api/user'
import { getToken } from '@/utils'
import Contracts from '@/assets/contracts.js'
export default {
  components: {
  },

  data() {
    return {
       popupshow:false,
       value:'',
       popupTitle:'',
       placeholder:'',
       popupHZ:false,
       radio: '1',
       list:[],
       display_amount:0,
       TQamount:'',
       HZamount:'',
       info:'',
       active:0,
       currentPage:1,
       total:0,
       limit:10,
       czdisplay_amount:''
    };
  },
  created() {
  },
  mounted() {
     this.asset_flowList()
     this.getUserInfo()
        //  获取用户币币账户余额：
        let vm=this
        this.$contract_game.methods.balanceOf(getToken()).call().then(function (result) {
          vm.display_amount = Contracts.cic.to_display_amount(result)
                // 渲染可用余额 display_amount
        });
  },
  methods: {
    getUserInfo(){
      getUserInfo().then((res) => {
        if(res.data.code=='success'){
          this.info=res.data.data  
        }else{this.$toast(res.data.msg)}
                   
      }).catch((error) => { reject(error)})
    },
    tiqu(){
      if(this.TQamount==''){
        this.$toast('请输入数量')
        return
      }
      if(this.TQamount<=0){
        this.$toast('数量请大于0')
        return
      }
      let vm=this
      //提取
      console.log(getToken())
      if(this.popupTitle=='提取'){
        // 其中address为当前钱包地址
        vm.$contract_game.methods.balanceOf(getToken()).call().then(balance => {
            let bal = parseFloat(Contracts.cic.to_display_amount(balance));
            if (bal < parseFloat(this.TQamount)) {
                // 提示余额不足
                vm.$toast('余额不足')   
                return;
            }
            vm.$contract_game.methods.withdraw(getToken(), Contracts.cic.to_real_amount((this.TQamount)))
                      .send({from: getToken(), gas: 40000})
                      .on('transactionHash', function () {
                          // 提示提取成功，请等待区块确认
                          vm.$toast('提取成功，请等待区块确认')   
                      })
                      .on('error', function (err) {
                          // 提示错误 err.message
                          vm.$toast(err.messag)
                      });
        });
      }else{
        //充值
          // amount 为用户输入的充值数量，address 为当前钱包地址
          vm.$contract_game.methods.depositFromToken(getToken(), Contracts.cic.to_real_amount(this.TQamount)).send({from: getToken()})
              .on('transactionHash', function (h) {
                  vm.$toast('充值成功，请等待区块确认')   
                  // 充值成功，请等待区块确认
              }).on('error', function (err) {
                vm.$toast(err.messag)
              // 提示错误 err.message
          });
      }
      
      
    },
    onClick(name,title){
      this.currentPage=1
      this.asset_flowList()
    },
    asset_flowList(){
      let account_type='';
      if(this.active==0){
        account_type='packet'
      }else{
        account_type='wallet'
      }
      let data={
        page:this.currentPage,
        limit:this.limit,
        account_type:account_type
      }
      asset_flowList(data).then((res) => {
        if(res.data.code=='success'){
          this.list=res.data.data.items
         this.total=res.data.data.total   
        }else{this.$toast(res.data.msg)}
                  
      }).catch((error) => { reject(error)})
    },
    btnclick(type){
      this.popupTitle=type
      this.popupshow=true
      this.placeholder='请填写'+type+'数量'
      if(type=='充值'){
             this.$contract_game.methods.balanceOf(getToken()).call().then(function (result) {
          vm.display_amount = Contracts.cic.to_display_amount(result)
                // 渲染可用余额 display_amount
        });
        let vm=this
         vm.$contract_cic.methods.balanceOf(getToken()).call().then(function (result) {
                vm.czdisplay_amount = Contracts.cic.to_display_amount(result)
                console.log(vm.czdisplay_amount)
                // 渲染可用余额 display_amount
            });
      }
    },
    buy(){
      this.popupshow=false
    },
    huazhuang(){
      if(this.HZamount==''){
        this.$toast('请输入数量')
        return
      }
       if(this.HZamount<=0){
        this.$toast('数量请大于0')
        return
      }
      this.popupHZ=false
      if(this.radio=='1'){
        let data={
          amount:this.HZamount,
          ...this.$datapublic
        }
       transferscreate(data).then((res) => {
          this.$toast('划转成功')         
      }).catch((error) => { reject(error)})
      }else{
      //调用智能合约 address为当前钱包地址
      let vm=this
       this.$contract_game.methods.balanceOf(getToken()).call().then(balance => {
          let bal = parseFloat(Contracts.cic.to_display_amount(balance));
          if (bal < parseFloat(this.HZamount)) {
              // 提示余额不足
              vm.$toast('余额不足')
              return;
          }
          this.$contract_game.methods.depositForUser(Contracts.cic.to_real_amount(this.HZamount))
                        .send({from: getToken(),gas: '40000'})
                        .on('transactionHash', function (result) {
                            // 提示成功
                            vm.$toast('划转成功')
                        })
                        .on('error', function (err) {
                            // 提示失败: err.messag
                            vm.$toast(err.messag)
                        });
      });
        
      }
    }
  },
  filters: {
       
   },
};
</script>
