<template>
  <div class="page-container">
    <fg-header :title="title" />

    <user-info class="user-info" uStar ></user-info>
    <ul class="list" v-if="title=='攻略'">
      <li class="item" v-for="item in list" :key="item.id" @click="goTo('noticeDel',item.id)">
        <img class="icon" src='../../assets/images/icon/gonglue.png' alt="">
        <div class="notice_bg">{{item.title}}</div>
      </li>
    </ul>
    <div v-if="title=='公告'">
       <div class="notice-item" v-for="(item, index) in list" :key="index">
      <div class="notice-wrap">
        <div class="notice-content">
          <p class="notice-text" :class="{ close: item.close }">
            {{ item.content }}
          </p>
          <p class="notice-more" @click="item.close = !item.close">
            {{ item.close ? '展开' : '收起' }}
          </p>
        </div>
      </div>
    </div>
    </div>
   
  </div>
</template>

<script>
import { articles, noticeslist } from '@/api/home'
  export default {
    data() {
      return {
        title:'',
        close: false,
        list: [],
      }
    },
  mounted() {
    
    if(this.$route.params.title){
      localStorage.setItem('notice',this.$route.params.title);
      this.title=this.$route.params.title
    }else{
      setTimeout(() => {
        this.title= localStorage.getItem('notice')
     }, 100);
    }
   this.int()
  },
  methods: {
      goTo(name, title) {
        this.$router.push({ name, params: { title} })
      },
    int(){
      if(this.title=='公告'){
        noticeslist().then((res) => {
          if(res.data.code=='success'){
            res.data.data.forEach(item => {
              this.$set(item,'close'.true)
            });
            this.list= res.data.data
          }else{this.$toast(res.data.msg)}
        
        }).catch((error) => {reject(error)})
      }else{
        articles().then((res) => {
          if(res.data.code=='success'){ this.list= res.data.data}else{this.$toast(res.data.msg)}
         
        }).catch((error) => {reject(error)})
      }
     
    },
  }
  }
</script>

